import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import reportWebVitals from './reportWebVitals';
import logo from './customers.png';


class App extends React.Component
{
  constructor(props)
  {
    super(props);
    
    this.state = 
    {
      Customers: [
        {
          firstName: 'Alexa',
          lastName: 'Peterson',
          email: 'ap@yahoo.com',
          phone: '+1-204-555-0105',
          address: '123, Dalhousie Dr, MB E6A 4Y2, CA'
        }, 
        {
          firstName: 'Siri',
          lastName: 'Hernandez',
          email: 'sh@gmail.com',
          phone: '+1-343-555-0177',
          address: '45 Lake View Dr, ON K4P 7B8, CA'
        }, 
        {
          firstName: 'Cortana',
          lastName: 'Goodman',
          email: 'cg@hotmail.com',
          phone: '+1-418-555-0182',
          address: '6 Sandman Ave, QC B2G 2X8, CA'
        }],
        message: ''
    }
  }

  handleSubmit(e)
  {
    e.preventDefault();
    //const newCustomer = this.newCustomer.value;
    const {Customers} = this.state;
    const firstName = this.firstName.value;
    const lastName = this.lastName.value;
    const phone = this.phone.value;
    const email = this.email.value;
    const address = this.address.value;

    const newCustomer = 
    {
      id: this.state.id,
      firstName: this.firstName.value,
      lastName: this.lastName.value,
      phone: this.phone.value,
      email: this.email.value,
      address: this.address.value
    }

     // checks if a customer is already on the list
     const found = (CustomerList, newCustomer) => 
     {
      for(let c of CustomerList)
      {
        if(c.firstName === newCustomer.firstName && c.lastName === newCustomer.lastName)
        {
          return true;
        }
      }
     };  

    if(found(Customers, newCustomer))
    {
      this.setState({message: 'This customer already exists'})
    }
    else
    {
      newCustomer.firstName !== '' && newCustomer.lastName !== '' && newCustomer.email !== '' && this.setState({
          Customers: [...this.state.Customers, newCustomer],
          message: ''
        })
    }

      //this.Form.reset();
  }

  delete(c)
  {
    const newCustomers = this.state.Customers.filter(customer => 
      { 
        return customer !== c;
      });

      this.setState({
        Customers: [...newCustomers]
      })
  }

  render()
  {
    const {Customers, message} = this.state;
    //const item = buyItems.map( val => <li>{val}</li> )
    
    return <div>
      <header class="text-center">
        {/* <img src={image} /> */}
        <img src={logo} width="150px" height="150px" className="App-logo" alt="logo" />
        <h1>Customers Management App</h1>
      </header>

      <form onSubmit={(e) => (this.handleSubmit(e))} class="text-center">
        <div class="form-row">
          <div class="col"> 
            <label for="firstName">First Name</label>
            <input ref={input => this.firstName = input} type="text" class="form-control" id="firstName" placeholder="Add first name" />
          </div>
          <div class="col"> 
            <label for="lastName">Last Name</label>
            <input ref={input => this.lastName = input} type="text" class="form-control" id="lastName" placeholder="Add last name" />
          </div>
          <div class="col"> 
            <label for="phone">Phone No.</label>
            <input ref={input => this.phone = input} type="text" class="form-control" id="phone" placeholder="Add Phone number" />
          </div>
          <div class="col"> 
            <label for="email">Email</label>
            <input ref={input => this.email = input} type="text" class="form-control" id="email" placeholder="Add Email Address" />
          </div>
          <div class="col"> 
            <label for="address">Address</label>
            <input ref={input => this.address = input} type="text" class="form-control" id="address" placeholder="Add Street Address" />
          </div>
          <div class="col">
            <br />
            <button class="btn btn-primary" type="submit">Submit</button>
          </div>
        </div>
      </form>

      <div className="content">
        {
          message !== '' && <p className="message text-danger">{message}</p>
        }
        <table className="table">
          <thead>
            <tr>
              <th scope="col">First Name</th>
              <th scope="col">Last Name</th>
              <th scope="col">Phone</th>
              <th scope="col">Email</th>
              <th scope="col">Address</th>
              <th scope="col">Action</th>
            </tr>
          </thead>
          <tbody>
            {
              Customers.map((c) => 
              <tr key={c.firstName + " " + c.lastName}>
                <td>{c.firstName}</td>
                <td>{c.lastName}</td>
                <td>{c.phone}</td>
                <td>{c.email}</td>
                <td>{c.address}</td>
                <td>
                  <button onClick={(e) => this.delete(c)} type="button" className="btn btn-default btn-sm btn-danger">
                    delete
                  </button>
                </td>
              </tr>
              )
            }
          </tbody>
        </table>
        

      </div>

    </div> 
  }
}
 

ReactDOM.render (
    <App />,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
